.heading {
  font-family: $oswald;
  font-size: 36px;
  line-height: 100%;
  font-weight: normal;
  width: 100%;

  @media only screen and (min-width: $desktop) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 80px;
  }

  &--first-screen {
    color: white;
    text-align: center;
    margin-bottom: 0;
    margin-top: 27px;

    @media only screen and (min-width: $tablet) {
      font-weight: normal;
      font-size: 60px;
      line-height: 60px;
      color: #000000;
      max-width: 450px;
      text-align: left;
      margin-top: 65px;
      margin-left: 87px;
    }

    @media only screen and (min-width: $desktop) {
      margin-top: 225px;
      margin-left: 80px;
    }
  }

  &--how-it-works {
    margin-bottom: 39px;

    @media only screen and (min-width: $tablet) {
      font-size: 60px;
      line-height: 60px;
      text-align: left;
      width: 100%;
      margin-bottom: 0;
      margin-top: 6px;
    }

    @media only screen and (min-width: $tablet) {
      margin-top: 0;
    }
  }

  &--sample {
    margin-bottom: 20px;

    @media only screen and (min-width: $tablet) {
      width: 100%;
      font-size: 60px;
      line-height: 60px;
      margin-top: 45px;
    }

    @media only screen and (min-width: $desktop) {
      margin-top: 63px;
    }
  }


  &--form {
    width: 100%;
    font-size: 36px;
    line-height: 36px;
    padding-left: calc((100vw - 280px) / 2);

    @media only screen and (min-width: $tablet) {
      width: unset;
      font-size: 60px;
      line-height: 60px;
      text-align: center;
      margin-left: -278px;
      margin-top: 67px;
      padding-left: 0;
    }

    @media only screen and (min-width: $desktop) {
      width: 100%;
      margin-left: 0;
      margin-top: 77px;
      margin-bottom: 0;
      text-align: left;
    }
  }

  &--catalog {
    margin-top: 28px;
    margin-bottom: 37px;
    padding-left: calc((100vw - 280px) / 2);

    @media only screen and (min-width: $tablet) {
      font-size: 60px;
      line-height: 60px;
      margin-bottom: 80px;
      margin-top: 69px;
      padding: 0;
    }

    @media only screen and (min-width: $desktop) {
      margin-top: 77px;
    }
  }

  &--extra {
    font-size: 24px;
    line-height: 24px;
    margin: 28px auto 34px auto;
    display: flex;
    width: 100vw;
    align-items: baseline;

    @media only screen and (min-width: $tablet) {
      font-size: 36px;
      line-height: 36px;
      margin-top: 6px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 0;
    }

    @media only screen and (min-width: $desktop) {
      margin-top: 17px;
      margin-bottom: 0;
    }


    &::after {
      border-top: 2px solid $base-color;
      padding: 0 0 0.3em;
      margin-left: 22px;
      flex-grow: 1;
      content: "";

      @media only screen and (min-width: $tablet) {
        margin-left: 42px;
      }
    }


    &::before {
      content: "";
      margin-right: calc((100vw - 320px) / 2 + 20px);

      @media only screen and (min-width: $tablet) {
        border-top: 2px solid $base-color;
        padding: 0 0 0.3em;
        margin-right: 38px;
        width: calc((100vw - 768px) / 2 + 30px);
        content: "";
      }

      @media only screen and (min-width: $desktop) {
        width: calc((100vw - 1440px) / 2 + 65px);
      }
    }

    & br {
      @media only screen and (min-width: $tablet) {
        display: none;
      }
    }
  }
}
