.sub-heading {
  font-family: $oswald;
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-size: 14px;
  margin-top: -265px;

  @media only screen and (min-width: $tablet) {
    margin-top: 41px;
    margin-bottom: 49px;
    font-weight: normal;
    font-size: 20px;
    line-height: 20px;
    color: #000000;

    &--first-screen {
      margin-left: 88px;

      @media only screen and (min-width: $desktop) {
        margin-top: 40px;
        margin-left: 80px;
      }
    }
  }
}
